.swiper {
  width: 100%;
  height: 430px;
  margin-top: -50px;
}

.swiper-slide {
  /*text-align: center;
  font-size: 18px;
  background: #fff;*/

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: flex;
  width: 265px;
  height: 100%;
  /*object-fit: cover;*/
  align-items: center;
  margin-bottom: 1.5rem;
}


